import { Component, ViewChild,OnInit,TemplateRef } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';

import { MatDialog } from '@angular/material/dialog';



@Component({
  selector: 'app-badges',
  templateUrl: './badges.component.html',
  styleUrls: ['./badges.component.css']
})
export class BadgesComponent implements OnInit {
  displayedColumns: string[] = ['position', 'Badge','Badgediscription','status','Action'];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  badges=[
    {Badge:'assets/images/badge.png'}
  ];
  Actions=[
    {
      Action:'assets/images/eye.png'
    },
    {
      Action:'assets/images/add.png'
    },
    {
      Action:'assets/images/delete.png'
    }
  ];
  constructor(private dialog: MatDialog){}

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  ngOnInit() {
    this.dataSource.paginator = this.paginator;
  }
  openDialogWithTemplateRef(templateRef: TemplateRef<any>) {
    this.dialog.open(templateRef);
  }
}

export interface PeriodicElement {
 
  position: number;
 
  Badge:any;
 
  status:any;

  Action:any;
  
  Badgediscription:any;
 

} 


const ELEMENT_DATA: PeriodicElement[] = [
  {position: 1,Badge:'assets/images/badge.png',Badgediscription:'Will be assigned on year completion in organization.',status:'active',Action:'assets/images/add.png' },
  {position: 2,Badge:'assets/images/badge.png',Badgediscription:'Will be assigned on full monthly attendance.',status:'active',Action:'assets/images/add.png' },
  {position: 1,Badge:'assets/images/badge.png',Badgediscription:'Delivered project on time with less than 20 bugs',status:'active',Action:'assets/images/add.png' },
  {position: 1,Badge:'assets/images/badge.png',Badgediscription:'Project delivered on or before deadline.',status:'active',Action:'assets/images/add.png' }
 
];