import { Component, ViewChild,OnInit,TemplateRef } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';



@Component({
  selector: 'app-moderator',
  templateUrl: './moderator.component.html',
  styleUrls: ['./moderator.component.css']
})

export class ModeratorComponent implements OnInit {
  displayedColumns: string[] = ['position', 'User', 'email','Badge','userType','Membersince','status','Action'];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  token:any
 
  constructor(private router:Router, private dialog: MatDialog) 
  {
  
  
   
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  ngOnInit() {
    this.dataSource.paginator = this.paginator;
    
  }
  openDialogWithTemplateRef(templateRef: TemplateRef<any>) {
    this.dialog.open(templateRef);
  }
  logout(){
    console.log('User signed out. ' + localStorage.length);
    localStorage.clear();
    this.token=null;
    console.log('User signed out. ' + localStorage.length);
  }
  add() {
    console.log("Adding record..");
  }
//   goToPage(position) {     
//     this.router.navigate(['/create'], { queryParams: { id: position } }); 
// }
//  (click)="openDialogWithTemplateRef(myDialog)"
}

