import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProfileDetailsComponent } from './profile-details/profile-details.component';
import { LoginComponent } from './login/login.component';
import { UserDetailsComponent } from './user-details/user-details.component';
import { ModeratorComponent } from './moderator/moderator.component';
import { ModeratorSectionComponent } from './user/moderator-section/moderator-section.component';
import { AssignBadgeComponent } from './assign-badge/assign-badge.component';
import { BadgesComponent } from './badges/badges.component';
import { CreateBadgeComponent } from './create-badge/create-badge.component';
import { PracticeComponent } from './practice/practice.component';
import { UpdateComponent } from './update/update.component';
import { ViewComponent } from './view/view.component';


const routes: Routes = [
  {
    path:'', redirectTo: '/login', pathMatch:'full'
  },
  {
    path:'login', component:LoginComponent
  },
  {
    path:'profile', component:ProfileDetailsComponent
  },
 
  {
    path:'userdetails', component:UserDetailsComponent
  },
  {
    path:'moderator', component:ModeratorComponent
  },
  {
    path:'assign-Badge', component:AssignBadgeComponent
  },
  {
    path:'badges', component:BadgesComponent
  },
  {
    path:'create', component: CreateBadgeComponent
  },{
    path:'practice', component: PracticeComponent
  },
  {
    path:'update', component: UpdateComponent
  },
  {
    path:'view', component: ViewComponent
  }

  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
